<template>
    <div class="header">
        <div class="mobileHeader">
            <a href="#" class="logoTitle">Nekocup Blog</a>
            <div class="menu">
                <div class="hambergerIcon" :class="{open:open}"/>
            </div>
            <i @click="toggleMenu()"></i>
            <ul :class="{openMenu:open, notOpenMenu:!open}">
                <li><a href="#">首頁</a></li>
            </ul>
        </div>
        <div class="table">
            <a href="#" class="logoTitle">Nekocup Blog</a>
            <ul>
                <li><a href="#">首頁</a></li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            open:false
        }
    },
    methods:{
        toggleMenu(){
            this.open = !this.open
        }
    }
}
</script>
<style lang="scss">

    .header {
        background: rgba($color: #383838, $alpha: .8);     
        .logoTitle{
            font-size: 1.25rem;
            color: #FFFFFF;
            display: block;
            font-weight:800;
            padding: 1em;    
            width: 200px;    
            transform: translateY(4px);    
        }
        ul {
            display: flex;
            padding: 1.3em;
            li {
                a {
                    display: block;
                    padding-right: 1em;
                    color: #aaaaaa;
                    &:hover {
                        color: #d1d1d1;
                    }
                }
            }
        }
        .mobileHeader {
            background: rgba($color: #383838, $alpha: .8); 
            position: fixed;
            width: 100%;
            top:0;
            ul {
                box-sizing: border-box;
                position: fixed;
                width: 100%;
                background: rgba($color: #444444, $alpha: .9);
                border-top: 1px solid rgb(216, 201, 183);
                box-shadow: 0px 1px 4px black;
                transition: right .5s;
            }
        }
        .table {
            height: 200px;
            width: 85%;
            max-width: 1100px;
            margin: auto;
            display: none;
        }
        i {
            display: block;
            width: 30px;
            height: 30px;
            position: absolute;
            right: 16px;
            top: 20px;
        }
    }
    .notOpenMenu {
        right: 1000px;
    }
    .openMenu{
        right: 0px;
    }
    @media screen and (min-width: 768px){
        .header {
            .mobileHeader {
                display: none;
            }
            .table {
                display: block;
            }
            .logoTitle {
                font-size: 1.5rem;
                padding-top: 2em;
            }
            ul {
                padding-top: 1em;
            }
        }
    }
</style>